import React from 'react';
import theme from './../../../css/App.module.css';
import style from './css/AboutPage.module.css';

function AboutPage() {
	return (
		<main className={`${style.aboutPage}`}>
			<div className="container">
				<div className="row">
					<div className="col-12">
						<div className={`${style.aboutPage__text}`}>
							<div className={`${style.topLeftCorner} ${theme.topLeftCorner}`}></div>
							<h2>О нас</h2>
							<p>ТОО "ДинАс Сервис" мы осуществляем продвижение и оптово-розничную продажу запасных частей
							к автомобилям КАМАЗ, ГАЗ, УАЗ, ВАЗ,ПАЗ и т.д на всей территории Республики Казахстан.</p>
							<p>Многолетний опыт сотрудничества с крупнейшими авто производителями запасных частей а также
							Сертификат
							на звание лидер года 2018 выданное Оргкомитетом Национального бизнес-рейтинга в Республике
							Казахстан
							подтверждает репутацию надежного партнера и серьезного игрока в авто бизнесе. Преимуществом нашей
							компании являются высокое качество поставляемой продукции, гарантии заводов - производителей,
							широкая
							география продаж и четкая логистика.
						</p>
							<h4>История</h4>
							<p>В 2019 году нам исполнилось 5 лет со дня основания.
							За это время наша компания стала лидером в своей отрасли.</p>
							<ul>
								<li>2014 год – Открытие компании</li>
								<li>2017 год – Заключение контракта с ТОО «ОзенМунайСервис» </li>
								<li>2018 год - Заключение контракта с ТОО «ОзенМунайСервис» </li>
								<li>2018 год – Заключение контракта с АО «УзеньПромГеоФизика» </li>
								<li>2018 год– Заключение контракта с АО «ОзенМунайГаз» </li>
								<li>2019 год – Заключение контракта с ТОО «Бургылау» </li>
							</ul>
							<h4>Структура и география работы</h4>
							<p>Выгодные условия сотрудничества, широкий ассортимент продукции, отлаженная логистика
							- все это неотъемлемая составляющая успешной совместной работы.</p>
							<p>Мы обеспечиваем продукцией и ведем переговоры с клиентами, где бы они не находились географически
							и стремимся к выходу на новые территории и расширению зоны охвата продаж.
						</p>
						</div>
					</div>
				</div>
			</div>
		</main>
	);
}

export default AboutPage;
